                                    </b-input-group>
                                    <p v-show="formInscrit.numero !== '' && validationMaxNumero === false" class="text-danger small ml-n4 mt-1 mb-n1">Le champ num&eacute;ro doit tre compris entre 1 et 9999.</p>
                                </div>

                            </div>
                            <div class="row row-cols-2">
                                <div class="col col-lg-3">
                                    <b-input-group class="mb-2 mr-sm-2 mb-sm-0">
