                                        <b-form-input type="tel" v-model="gsm_national" :state="validationGsm_national"
                                                      name="gsm_national" id="gsm_national" placeholder="+32"
                                                      pattern="[\+]\d{2}" style="max-width:85px;" maxlength="4">
                                        </b-form-input>
                                        <b-form-input type="tel" v-model="formInscrit.gsm" :state="validationGsm || checkGsm"
                                                      name="gsm" id="gsm" placeholder="0xxx/xxx.xxx"
                                                      pattern="([\/\.\s\(\)]?[0-9]){9,16}" maxlength="16"
                                                      class="form-control" :class="{ 'is-invalid': formInscrit.errors.has('gsm') || validationGsm === false || checkGsm === false }">
                                        </b-form-input>
                                        <b-input-group-append is-text>
                                            <b-form-checkbox v-model="champsObligatoires.gsm" class="mb-2 mr-sm-2 mb-sm-0">ND</b-form-checkbox>
                                        </b-input-group-append>
                                    </b-input-group>
                                    <p v-show="validationGsm_national === false" class="text-danger small mt-1 mb-n1">Le prfixe doit contenir au minimum 3 caract&egrave;res.</p>
                                    <p v-show="validationGsm === false" class="text-danger small mt-1 mb-0">Le numro de gsm doit contenir au minimum 9 caract&egrave;res.</p>
                                </div>
                                <div class="col col-lg-6">
                                    <b-input-group class="mb-2 mr-sm-2 mb-sm-0">
                                        <b-input-group-prepend is-text>
                                            <span class="text-light-interface font-weight-bold">T&eacute;l. fix</span>
                                        </b-input-group-prepend>
                                        <b-form-input type="tel" v-model="tel_national" :state="validationTel_national"
                                                      name="tel_national" id="tel_national" placeholder="+32"
                                                      pattern="[\+]\d{2}" style="max-width:85px;" maxlength="4">
                                        </b-form-input>
                                        <b-form-input type="tel" v-model="formInscrit.tel" :state="validationTel || checkTel"
                                                      name="tel" id="tel" placeholder="0xx/xxx.xxx"
                                                      pattern="([\/\.\s\(\)]?[0-9]){8,15}" maxlength="15"
                                                      class="form-control" :class="{ 'is-invalid': formInscrit.errors.has('tel') || validationTel === false || checkTel === false }">
                                        </b-form-input>
                                        <b-input-group-append is-text>
                                            <b-form-checkbox v-model="champsObligatoires.tel" class="mb-2 mr-sm-2 mb-sm-0">ND</b-form-checkbox>
                                        </b-input-group-append>
                                    </b-input-group>
                                    <p v-show="validationTel_national === false" class="text-danger small mt-1 mb-n1">Le prfixe doit contenir au minimum 3 caract&egrave;res.</p>
                                    <p v-show="validationTel === false" class="text-danger small mt-1 mb-0">Le numro de tlphone doit contenir au minimum 8 caract&egrave;res.</p>
                                </div>
                            </div>
                        </form>
                        <span class="font-weight-light font-italic text-light-interface"><small>Tous les champs avec * sont obligatoires</small></span>
                    </div>
                    <div class="text-right" v-show="loading === true">
                        <v-btn class="btn-interface text-light mt-n5" @click="validationStepOne()">Continuer</v-btn>
                    </div>
                    <div v-show="loading === false">
                        <v-row class="text-center text-interface mt-10">
                            <v-col class="d-flex flex-column justify-center align-center">
                                <v-progress-circular :size="70" :width="10" color="interface" indeterminate></v-progress-circular>
                                <span class="mt-5">Chargement...</span>
                            </v-col>
                        </v-row>
                    </div>
                </v-stepper-content>
                <!-- Contenu partie 2 : Gnralites (suite) -->
                <v-stepper-content step="2">
                    <div class="step-content mb-6">
                        <form>
                            <div class="row">
                                <div class="col">
                                    <b-input-group class="mb-2 mr-sm-2 mb-sm-0">
                                        <b-input-group-prepend is-text>
                                            <span class="text-light-interface font-weight-bold">Commentaire(s)</span>
                                        </b-input-group-prepend>
                                        <b-form-textarea v-model="formInscrit.commentaire_inscrit" rows="5" class="form-control"
                                                         name="commentaire_inscrit" id="commentaire_inscrit" placeholder="Entrez  un commentaire...">
                                        </b-form-textarea>
                                        <has-error :form="formInscrit" field="commentaire_inscrit"></has-error>
                                    </b-input-group>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div class="text-right">
                        <v-btn class="btn btn-danger text-light mt-n5" @click="step = 1">Retour</v-btn>
                        <v-btn class="btn-interface text-light mt-n5" @click="goToStepThree()">Continuer</v-btn>
                    </div>
                </v-stepper-content>
                <!-- Contenu partie 3 : Choix des tags -->
                <v-stepper-content step="3">
                    <div class="step-content mb-6">
                        <div class="container">
                            <div class="row">
                                <div class="col">
                                    <b-form-group label="Slectionner les centres d'intrts dans la liste droulante">
                                        <b-form-tags input-id="tags" v-model="inscritTags" size="lg" add-on-change no-outer-focus>
                                            <template v-slot="{ tags, inputAttrs, inputHandlers, disabled, removeTag }">
                                                <ul v-if="tags.length > 0" class="list-inline d-inline-block mb-1">
                                                    <li v-for="tag in tags" :key="tag" class="list-inline-item">
                                                        <b-form-tag
                                                            @remove="removeTag(tag)"
                                                            :title="tag"
                                                            :disabled="disabled"
                                                            variant="success"
                                                            class="mb-1"
                                                        >{{ tag }}</b-form-tag>
                                                    </li>
                                                </ul>
                                                <b-form-select v-bind="inputAttrs" v-on="inputHandlers" class="mt-1">
                                                    <template v-slot:first>
                                                        <!-- This is required to prevent bugs with Safari -->
                                                        <option disabled value="">Choisissez un tag...</option>
                                                        <option v-for="tag in listTags" :key="tag.id" :value="tag.tag">{{ tag.tag }}</option>
                                                    </template>
                                                </b-form-select>
                                            </template>
                                        </b-form-tags>
                                    </b-form-group>
                                </div>
                                <div class="col">
                                    <b-form-group label="Si vous ne trouvez pas le centre d'intrt dans la liste droulante, crez-le :">
                                        <b-form-tags
                                            input-id="tags"
                                            v-model="newTags"
                                            :tag-validator="validator"
                                            tag-variant="success"
                                            tag-pills
                                            size="md"
                                            separator=",."
                                            @tag-state="onTagState"
                                            placeholder="Entrez un new tag spar d'une virgule ou d'un point"
                                        ></b-form-tags>
                                        <template #description>
                                            <div id="tags-validation-help">
                                                Un nouveau tag doit tre compris entre 3 et 50 caractres.<br>Sparer les nouveaux tags par une virgule ou un point ou appuyer sur Add.
                                            </div>
                                        </template>
                                    </b-form-group>
                                    <v-btn class="btn-interface text-light" @click="createTags()">Crer les tags</v-btn>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="text-right">
                        <v-btn class="btn btn-danger text-light" @click="step = 2">Retour</v-btn>
                        <v-btn class="btn-success text-light" @click="storeInscrit()">Finaliser</v-btn>
                    </div>
                </v-stepper-content>
            </v-stepper-items>
        </v-stepper>
    </div>
</template>

<script>
    import {Form} from "vform";
    import moment from 'moment';

    export default {
        name: "CreateInscrit",
        data() {
            return {
                loading: false,
                step: 1,
                tel_national: '+32',
                gsm_national: '+32',
                formInscrit : new Form({
                    id: '',
                    nom: '',
                    prenom: '',
                    genre: null,
                    date_naissance: '',
                    rue: '',
                    numero: '',
                    boite: '',
                    ville_id: null,
                    email: '',
                    tel: null,
                    gsm: null,
                    commentaire_inscrit: '',
                    newsletter: false,
                    prospect: 1,
                    age: null,
                    num_national: null,
                    statut_legal: null,
                    diplome: null,
                    duree_chomage: null,
                    moyen_recrutement: null,
                    groupe_social: null,
                    tags: [],
                    recrutement: null,
                    formation: null,
                    current_user: null,
                    pmtic_module_1: false,
                    pmtic_module_2: false,
                    pmtic_module_3: false,
                }),
                champsObligatoires: {
                    'genre' : false,
                    'date_naissance' : false,
                    'ville_id' : false,
                    'email' : false,
                    'tel' : true,
                    'gsm' : false,
                },
                yearsAgo: moment().subtract(15, 'years').format('YYYY'),
                villes: [],
                inscrit: [],
                listTags: [],
                newTags: [],
                inscritTags : [],
                formTag : new Form({
                    tag: null,
                }),
                test_tag: 0,
                tabTags: [],
            }
        },
        watch: {
            choix_tag: function(newTag) {
                this.newTags.push(newTag);
                this.choix_tag = null;
            },
            checkNewsletter: function(newValue) {
                if(this.champsObligatoires.email === true) {
                    this.champsObligatoires.email = newValue;
                }
            }
        },
        mounted() {
            console.log('CreateInscrit component mounted');
            this.getDatas();
        },
        computed: {
            validationNom() {
                return this.formInscrit.nom.length > 1;
            },
            validationMaxNom() {
                return this.formInscrit.nom.length < 190;
            },
            validationPrenom() {
                return this.formInscrit.prenom.length > 1;
            },
            validationMaxPrenom() {
                return this.formInscrit.prenom.length < 190;
            },
            validationGenre() {
                if(this.champsObligatoires.genre === false) {
                    return this.formInscrit.genre != null;
                }
            },
            validationNaissance() {
                if(this.champsObligatoires.date_naissance === false) {
                    return this.formInscrit.date_naissance !== '';
                }
            },
            formattedDateNaissance () {
                return this.formInscrit.date_naissance ? moment(this.formInscrit.date_naissance).format('L') : ''
            },
            validDateNaissance() {
                if(this.formInscrit.date_naissance !== null && this.formInscrit.date_naissance.length > 0) {
                    return moment().diff(moment(this.formInscrit.date_naissance), 'years', true) < 15;
                }
            },
            validationMaxRue() {
                return this.formInscrit.rue.length < 190;
            },
            validationMaxNumero() {
                return Number(this.formInscrit.numero) > 0 && Number(this.formInscrit.numero) < 10000;
            },
            validationMaxBoite() {
                return this.formInscrit.boite.length <= 10;
            },
            validationVille() {
                if(this.champsObligatoires.ville_id === false) {
                    return this.formInscrit.ville_id != null;
                }
            },
            validationEmail() {
                if(this.champsObligatoires.email === false) {
                    return this.formInscrit.email !== '';
                }
            },
            checkEmail() {
                if(this.formInscrit.email !== '') {
                    return this.validEmail(this.formInscrit.email);
                }
            },
            validationMaxEmail() {
                return this.formInscrit.email.length < 190;
            },
            checkNewsletter() {
                if(this.formInscrit.email.length === 0) {
                    return this.formInscrit.newsletter !== true;
                }
            },
            validationTel_national() {
                if(this.champsObligatoires.tel === false && this.formInscrit.tel_national !== null) {
                    return this.tel_national.length > 2 && this.tel_national.length < 5;
                }
            },
            checkTel() {
                if(this.champsObligatoires.tel === false) {
                    return this.formInscrit.tel !== null;
                }
            },
            validationTel() {
                if(this.champsObligatoires.tel === false && this.formInscrit.tel !== null) {
                    return this.formInscrit.tel.length > 7 && this.formInscrit.tel.length < 16;
                }
            },
            validationGsm_national() {
                if(this.champsObligatoires.gsm === false && this.formInscrit.gsm_national !== null) {
                    return this.gsm_national.length > 2 && this.gsm_national.length < 5;
                }
            },
            checkGsm() {
                if(this.champsObligatoires.gsm === false) {
                    return this.formInscrit.gsm !== null;
                }
            },
            validationGsm() {
                if(this.champsObligatoires.gsm === false && this.formInscrit.gsm !== null) {
                    return this.formInscrit.gsm.length > 8 && this.formInscrit.gsm.length < 17;
                }
            },
        },
        methods: {
            retour() {
                Suppression.fire({
                    text: "Si vous continuez, vous retournez  la liste des prospects!",
                }).then((result) => {
                    if (result.value) {
                        this.$router.push('/prospects')
                    }
                });
            },
            getDatas() {
                this.loading = false;
                this.getVilles();
                this.getListTags();
                this.currentUser();
                this.loading = true;
            },
            checkDateNaissance() {
                if(this.champsObligatoires.date_naissance === false){
                    this.champsObligatoires.date_naissance = true;
                } else {
                    this.champsObligatoires.date_naissance = false;
                }
            },
            validEmail: function (email) {
                let re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            },
            validation(test, message){
                if(test) {
                    Snackbar.fire(message);
                    return true;
                }
                return false;
            },
            currentUser() {
                this.formInscrit.current_user = this.$store.getters.currentUser.id;
            },
            onContext(ctx) {
                // The date formatted in the locale, or the `label-no-date-selected` string
                this.formatted = ctx.selectedFormatted
                // The following will be an empty string until a valid date is entered
                this.selected = ctx.selectedYMD
            },
            getVilles() {
                axios.get('api/villes/')
                    .then(response => {
                        this.villes = response.data;
                    })
                    .catch(error => {
                        console.log(error.response);
                        Snackbar.fire({
                            title: 'Problme avec la rcupration de la liste des villes',
                            timer: undefined,
                        })
                    })
            },
            getListTags() {
                axios.get('api/tags/all')
                    .then(response => {
                        this.listTags = response.data.tags;
                    })
                    .catch(error => {
                        console.log(error.response);
                        Snackbar.fire({
                            title: 'Problme avec la rcupration de la liste des tags',
                            timer: undefined,
                        })
                    })
            },
            validationStepOne() {
                if(this.validation(this.formInscrit.nom === '', "Vous n'avez pas rempli le nom !")) {
                } else if(this.validation(this.formInscrit.prenom === '', "Vous n'avez pas rempli le prnom !")) {
                } else if(this.validation(this.champsObligatoires.nom === true && this.champsObligatoires.prenom === true, "Il faut au moins le nom ou le prnom !")) {
                } else if(this.validation(this.formInscrit.nom.length < 2, "Le nom doit tre compos d'au moins 2 lettres !")) {
                } else if(this.validation(this.formInscrit.prenom.length < 2, "Le prnom doit tre compos d'au moins 2 lettres !")) {
                } else if(this.validation(this.formInscrit.nom !== '' && !this.validationMaxNom, "Le champ nom ne peut contenir plus de 190 caractres !")) {
                } else if(this.validation(this.formInscrit.prenom !== '' && !this.validationMaxPrenom, "Le champ prnom ne peut contenir plus de 190 caractres !")) {
                } else if(this.validation(this.formInscrit.genre == null && this.champsObligatoires.genre === false, "Vous n'avez pas rempli le genre ou cochez la case 'non disponible' (ND) correspondante !")) {
                } else if(this.validation(this.formInscrit.date_naissance === '' && this.champsObligatoires.date_naissance === false, "Vous n'avez pas rempli la date de naissance ou cochez la case 'non disponible' (ND) correspondante !")) {
                } else if(this.validation(this.formInscrit.date_naissance !== '' && this.validDateNaissance, "La date de naissance ne peut tre une date future ou datant aprs " + this.yearsAgo + " !")) {
                } else if(this.validation(this.formInscrit.rue !== '' && !this.validationMaxRue, "Le champ rue ne peut contenir plus de 190 caractres !")) {
                } else if(this.validation(this.formInscrit.numero !== '' && Number(this.formInscrit.numero) <= 0, 'Le numro doit tre positif et non nul !')) {
                } else if(this.validation(this.formInscrit.numero !== '' && Number(this.formInscrit.numero) > 9999, 'Le numro doit tre infrieur  10000 !')) {
                } else if(this.validation(this.formInscrit.boite !== '' && !this.validationMaxBoite, "Le champ boite ne peut contenir plus de 10 caractres !")) {
                } else if(this.validation(this.formInscrit.ville_id == null && this.champsObligatoires.ville_id === false, "Vous n'avez pas rempli la ville ou cochez la case 'non disponible' (ND) correspondante !")) {
                } else if(this.validation(this.formInscrit.email === '' && this.champsObligatoires.email === false && this.checkNewsletter === true, "Vous n'avez pas rempli l'email ou cochez la case 'non disponible' (ND) correspondante !")) {
                } else if(this.validation(this.formInscrit.email !== '' && this.validEmail(this.formInscrit.email) === false, "Vous n'avez pas renseign un email valide !")) {
                } else if(this.validation(this.formInscrit.email !== '' && !this.validationMaxEmail, "Le champ email ne peut contenir plus de 190 caractres !")) {
                } else if(this.validation(this.checkNewsletter === false, "L'email est requis pour la newsletter !")) {
                } else if(this.validation(this.champsObligatoires.gsm === false && (this.gsm_national === '' || this.formInscrit.gsm === '' || this.gsm_national === null || this.formInscrit.gsm === null), "Vous n'avez pas rempli tous les champs du n de gsm ou cochez la case 'non disponible' (ND) correspondante !")) {
                } else if(this.validation(this.champsObligatoires.gsm === false && (this.gsm_national.length < 3 || this.formInscrit.gsm.length < 9 ), "Il n'y pas le minimum de caractres requis dans un des champs du n de gsm !")) {
                } else if(this.validation(this.champsObligatoires.tel === false && (this.tel_national === '' || this.formInscrit.tel === '' || this.tel_national === null || this.formInscrit.tel === null), "Vous n'avez pas rempli le tl. fix ou cochez la case 'non disponible' (ND) correspondante !")) {
                } else if(this.validation(this.champsObligatoires.tel === false && (this.tel_national.length < 3 || this.formInscrit.tel.length < 8 ), "Il n'y pas le minimum de caractres requis dans un des champs du n de tl. fix !")) {
                } else {
                    if(this.formInscrit.ville_id == null) {
                        this.formInscrit.ville_id = 2766
                    }
                    this.step = 2;
                }
            },
            goToStepThree() {
                this.step = 3;
            },
            onTagState(valid, invalid, duplicate) {
                this.validTags = valid
                this.invalidTags = invalid
                this.duplicateTags = duplicate
            },
            validator(tag) {
                return tag.length > 2 && tag.length < 50
            },
            createTags() {
                if (this.validation(this.newTags.length < 1, 'Pas de nouveau tag  crer !')) {
                } else {
                    for(let x = 0; x < this.newTags.length; x++) {
                        this.test_tag = 0;
                        for(let y = 0; y < this.listTags.length; y++) {
                            if(this.newTags[x] !== this.listTags[y].tag) {
                                this.test_tag++;
                            } else {
                                Snackbar.fire('Tag dj existant !');
                                this.newTags.splice([x],0);
                            }
                        }
                        if(this.test_tag === this.listTags.length) {
                            this.formTag = new Form({
                                tag: this.newTags[x],
                            });
                            this.storeTag();
                        }
                    }
                }

            },
            storeTag() {
                this.$Progress.start();
                this.formTag.tag = this.formTag.tag.toLowerCase();
                this.formTag
                    .post('api/tags/create')
                    .then(response => {
                        if (this.formTag.successful) {
                            this.$Progress.finish();
                            Toast.fire('Tag(s) cr(s)');
                            this.getListTags();
                            this.newTags = [];
                        }
                    })
                    .catch(error => {
                        this.$Progress.fail();
                        console.log(error.response);
                        Snackbar.fire('Problme avec la cration du tag !');
                    })
            },
            storeInscrit() {
                this.$Progress.start();
                for(let x = 0; x < this.inscritTags.length; x++) {
                    for(let y = 0; y < this.listTags.length; y++) {
                        if(this.inscritTags[x] === this.listTags[y].tag) {
                            this.tabTags.push(this.listTags[y].id);
                        }
                    }
                }
                this.formInscrit.tags = this.inscritTags;
                if(this.formInscrit.tel !== null) {
                    let telTab = this.formInscrit.tel.split('');
                    if(telTab[0] == 0) {
                        telTab.splice(0, 0, '(');
                        telTab.splice(2, 0, ')');
                        this.formInscrit.tel = telTab.join('');
                    } else if(telTab[0] !== '(' && telTab[1] !== '0' && telTab[2] !== ')') {
                        telTab.splice(0, 0, '(0)');
                        this.formInscrit.tel = telTab.join('');
                    }
                    this.formInscrit.tel = this.tel_national + this.formInscrit.tel;
                }
                if(this.formInscrit.gsm !== null) {
                    let gsmTab = this.formInscrit.gsm.split('');
                    if(gsmTab[0] == 0) {
                        gsmTab.splice(0, 0, '(');
                        gsmTab.splice(2, 0, ')');
                        this.formInscrit.gsm = gsmTab.join('');
                    } else if(gsmTab[0] !== '(' && gsmTab[1] !== '0' && gsmTab[2] !== ')') {
                        gsmTab.splice(0, 0, '(0)');
                        this.formInscrit.gsm = gsmTab.join('');
                    }
                    this.formInscrit.gsm = this.gsm_national + this.formInscrit.gsm;
                }
                this.formInscrit
                    .post('api/inscrits/create')
                    .then(response => {
                        if(this.formInscrit.successful) {
                            this.$Progress.finish();
                            if(response.data.message && response.data.message === 'Personne existante !') {
                                Snackbar.fire({
                                    title: response.data.message + "\nVeuillez mettre  jour sa fiche...",
                                    timer: undefined
                                });
                                this.$router.push('/inscrits/show/' + response.data.inscrit.id);
                            } else {
                                Toast.fire('Inscription effectue');
                                this.$router.push('/inscrits/show/' + response.data.data.id);
                            }
                        }
                    })
                    .catch(error => {
                        console.log(error.response);
                        this.$Progress.fail();
                        Snackbar.fire('Problme avec l\'inscription du prospect !');
                    })
            },
        }
    }
</script>

